/* School Website*/
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  text-decoration: none;
}


/* Header and Navigation Bar */

#head {
  display: flex;
  flex-direction: column;
  height: 800px;
  position: relative;
}

#head::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background: url('https://images.unsplash.com/photo-1510070112810-d4e9a46d9e91?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=869&q=80') center center / cover no-repeat;
  z-index: -1;
  opacity: 0.5;
}

#navbar {
  display: flex;
  align-items: center;
  width: auto;
  margin: 10px auto;
  height: 100px;
  position: relative;
}

#dcm{
  display: flex;
  width: auto;
  margin-top: 20px;
  padding: 0 20px;
  margin-right: auto;
  margin-left: auto;
  font-family: 'Acme', sans-serif;
font-family: 'DM Serif Display', serif;
}

#dcm h2{
  padding: 0px 20px;
  text-align: center;
  font-size: 1.1rem;
}

#navbar::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: black;
  z-index: -1;
  opacity: 0.1;
}

#navbar img {
  height: 80px;
  width: 80px;
  border-radius: 90px;
  padding: 5px;
  margin: 1px 10px;
}

#navbar ul {
  display: flex;
  height: 50px;
  align-items: center;
  margin: 10px;
  position: relative;
  left: 1rem;
}

#navbar ul li {
  padding: 1px;
  margin: 5px;
  list-style: none;
  position: relative;
}

#navbar ul li a {
  font-size: 0.9rem;
  text-decoration: none;
  padding: 10px;
  font-family: Verdana;
  color: white;
  font-weight: 900;
}

#navbar ul li :hover{
  border-bottom: 2px solid white;
}

/* Section 1 */

#info {
  display: flex;
  flex-direction: column;
  width: 69rem;
  margin: auto;
  align-items: center;
  position: relative;
  bottom: 50px;
}

#info h1 {
  font-size: 4rem;
  font-weight: 700;
  color: black;
  text-shadow: 2px 2px 10px;
  font-family: 'Acme', sans-serif;
  font-family: 'DM Serif Display', serif;
}

#reality {
  text-align: center;
  width: 63rem;
  margin: 40px auto;
  color: black;
}

#gets{
  height: 50px;
  width: 200px;
  cursor: pointer;
  font-size: 1.3rem;
  background-color: transparent;
  border: none;
  margin: 20px auto;
  color: black;
}

#buttons{
  display: flex;
  align-items: center;
  margin-top: 20px;
}

#arrow{
  font-size: 2rem;
  cursor: pointer;
  height: 0px;
  margin-bottom: 4.5rem;
  margin-left: -5.5rem;
  color: black;
}


/* Services */

#services {
