  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-fill-mode: backwards;
  animation-iteration-count: infinite;
  font-size: 4rem;
  display: flex;
  justify-content: center;
  position: relative;
  cursor: pointer;
}

@keyframes varrow {
  from{
    top: 0;
  }to{
    top: 30px;
  }
}


@keyframes fly {
  from{
    transform: translateY(-300px);
  }to{
    transform: translateY(0px);
  }
}

