  text-align: left;
}

#rc1{
  display: flex;
}

#rc2{
  display: flex;
}

#nlines{
  height: 850px;
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  font-weight: 800;
}

#nlines::before{
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url('https://images.unsplash.com/photo-1502086223501-7ea6ecd79368?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=838&q=80')center center /cover no-repeat;
  z-index: -1;
  opacity: 0.3;
}

.nouter{
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: none;
}

.ninner{
  height: 30rem;
  width: 30rem;
  border: 2px solid orangered;
  margin: 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.ninner img{
  height: 150px;
  width: 150px;
  border-radius: 50%;
  margin-bottom: 30px;
  margin-top: 20px;
}

.ninner h2{
  color: white;
  font-size: 2rem;
  margin-bottom: 30px;
  border-bottom: 2px  solid orangered;
  text-align: center;
  font-family: 'DM Serif Display', serif;
}

.ninner p{
  color: white;
  font-size: 1.3rem;
  text-align: center;
  margin-bottom: 50px;
  padding: 10px;
  font-family: 'Comic Neue', cursive;
}

.nx{
  font-size: 3rem;
  background-color: transparent;
  border: none;
  color: red;
  position: absolute;
  top: 20px;
  right: 50px;
  cursor: pointer;
}

.nx:hover{
  opacity: 0.6;
}

#nheading{
  display: flex;
  position: absolute;   
  left: 100px;
  font-size: 2.5rem;
  width: 35rem;
  text-align: center;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20%;
  font-family: 'DM Serif Display', serif;
}

#nlist{
  position: absolute;
  right: 60px;
  font-family: 'Comic Neue', cursive;
}

#nlist h3{
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 50px;
  font-weight: 700;
  font-family: 'DM Serif Display', serif;
}

#nlist ul{
  list-style: none;
}

#nlist ul li{
  font-size: 1.5rem;
  padding: 10px 0;
}

#nlist ul li a{
  color: black;
}

#nlist ul li a:hover{
  color: #00BFFF;
  transition: all;
  transition-duration: 0.01s;
  transition-timing-function: ease-out;
}

#nlist ul li:hover{
  text-decoration: underline;
}

#row{
  height: auto;
  width: auto;
}

#dcm{
  font-family: 'Comic Neue', cursive;
}

#reality{
  font-family: 'Comic Neue', cursive;
}

